.MobileMenu{
    /* position: fixed; */
    background-color: #141414;
    color: white;
    font-size: 16px;
    line-height: 20px;
    font-weight: 500;
    height: 100vh;
    width: 40%;
    /* position: absolute; */
    /* transition: 1.5s; */
}
.MobileMenu ol{
    margin: 0;
    padding: 0;
    width: 100%;
    position: absolute;
}
.MobileMenu li,
.SecondMenu li,
.CatalogMenu li{
    width: 100%;
    border-bottom: 1px solid #3D3D3D;
    padding: 20px 0 20px 30px;
    transition: 500ms ease-out;
}
.fa-chevron-right{
    position: absolute;
    right: 20px;
}
.fa-angle-left{
    margin-right: 20px;
}
/* Тут настройки для Navigation.js  */
.menuClose{
    height: 100vh;
    width: 40%;
    position: fixed;
    left: -40%;
    z-index: 11;
    /* transition: 0.5s; */
    transition: 500ms ease-out;
    margin-top: 150px;
    background-color: #141414;
}
.menuEscape{
    overflow: hidden;
    height: 100vh;
    width: 40%;
    position: fixed;
    left: 0;
    z-index: 11;
    transition: 0.5s;
    margin-top: 150px;
    background-color: #141414;
    
}
.bodyClose.menuEscape{
    overflow: hidden;
    height: 100vh;
}
.SecondMenu{
    background-color: #141414;
    color: white;
    font-size: 16px;
    line-height: 20px;
    font-weight: 500;
    height: 100vh;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    transition: 500ms ease-out;
    /* opacity:1;
